import {LitElement, PropertyValueMap, html} from 'lit';
import { customElement, query, state } from 'lit/decorators.js';
import { QuillSnowStyle } from '../../styles/quill.snow';
import { Quill } from 'quill';

@customElement('altar-comment-editor')
export class AltarCommentEditor extends LitElement {

    @query('#quill-editor')
    quillElement!: HTMLElement;

    @state()
    quillEditor!: any;

    protected override firstUpdated(_changedProperties: PropertyValueMap<this>) {
        super.firstUpdated(_changedProperties);
        const options = {
            debug: 'info',
            modules: {
              toolbar: '#toolbar'
            },
            placeholder: 'Enter you comment...',
            readOnly: true,
            theme: 'snow'
        };
        this.quillEditor = new Quill(this.quillElement, options);
        
    }

  override render() {
    return html`
    <div id="quill-editor">
        <p>Hello World!</p>
        <p>Some initial <strong>bold</strong> text</p>
        <p><br></p>
    </div>
    `;
  }


  static override styles = QuillSnowStyle;
  

}

declare global {
  interface HTMLElementTagNameMap {
    'altar-comment-editor': AltarCommentEditor;
  }
}
